import { Ng2BootstrapModule }   from 'ngx-bootstrap';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule }   from '@angular/forms';
import { HttpModule }    from '@angular/http';
import { NgModule }      from '@angular/core';
import { StoreModule }   from '@ngrx/store';

import 'rxjs/Rx';
import 'hammerjs';

import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MaterialModule }          from '@angular/material';

import { MainSidebarActions } from './store/actions';
import { MainSidebarService } from './services';

import { AppRoutingModule } from './app.routing';

import {
  AppComponent,
  HomeComponent,
  HomePropositionsComponent,
  HomeToolbarComponent,
  MainSidebarComponent,
} from './components';

import { ActivityOverviewComponent } from './shared/components/activity-overview/activity-overview.component';

import { rootReducer } from './store/rootReducer';

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    HomePropositionsComponent,
    HomeToolbarComponent,
    MainSidebarComponent,
    ActivityOverviewComponent,
  ],
  imports: [
    Ng2BootstrapModule.forRoot(),
    BrowserModule,
    FormsModule,
    HttpModule,
    StoreModule.provideStore(rootReducer),
    AppRoutingModule,

    // Angular Material
    BrowserAnimationsModule,
    MaterialModule,
  ],
  providers: [
    MainSidebarActions,
    MainSidebarService,
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



