export interface MainSidebar {
  readonly opened : boolean;
};

export function createDefaultMainSidebar(): MainSidebar {
  return { opened : false };
};

export function setMainSidebarOpened(mainSidebar : MainSidebar, opened : boolean) : MainSidebar {
  return { opened };
}



