import { Component, ViewChild }  from '@angular/core';
import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';
import { Observable } from 'rxjs';

import { MainSidebar }        from '../../models/main-sidebar';
import { MainSidebarService } from '../../services';
import { MainSidebarActions } from '../../store/actions';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  animations: [
    trigger('mainSidebar', [
      state('true', style({
        width: '350px',
      })),
      state('false', style({
        width: '0px',
      })),
      transition('true => false', animate('10ms ease-in')),
      transition('false => true', animate('10ms ease-out'))
    ]),
  ],
})
export class AppComponent {

  @ViewChild('mainSidebar') mainSidebar : any;

  opened$ : Observable<string>;
  mainSidebarState: boolean;

  toggleSidebar() {
    console.log('toggleCalled')
    this.mainSidebarState = !this.mainSidebarState;
  }

  constructor(
    private mainSidebarService : MainSidebarService,
    public mainSidebarActions  : MainSidebarActions,
  ) {
    this.mainSidebarState = false;
    this.opened$ = this.mainSidebarService.getMainSidebar()
      .map((mainSidebar) => mainSidebar.opened.toString());
  }
}



