import { Injectable } from '@angular/core';
import { Store }      from '@ngrx/store';

import { createAction } from '../createAction';
import { AppState } from '../../models';

@Injectable()
export class MainSidebarActions {
  static readonly OPEN = 'MAIN-SIDEBAR_OPEN';
  static readonly CLOSE = 'MAIN-SIDEBAR_CLOSE';
  static readonly TOGGLE = 'MAIN-SIDEBAR_TOGGLE';

  constructor(private store : Store<AppState>) {}

  open() {
    this.store.dispatch(createAction(MainSidebarActions.OPEN));
  }

  close() {
    this.store.dispatch(createAction(MainSidebarActions.CLOSE));
  }

  toggle() {
    this.store.dispatch(createAction(MainSidebarActions.TOGGLE));
  }
};



