import { Action } from '@ngrx/store';

import { MainSidebar, createDefaultMainSidebar, setMainSidebarOpened } from '../../models/main-sidebar';
import { MainSidebarActions } from './main-sidebar.action';

export function mainSidebarReducer(
  mainSidebar : MainSidebar = createDefaultMainSidebar(),
  action: Action,
) : MainSidebar {
  switch (action.type) {
    case MainSidebarActions.OPEN:
      return setMainSidebarOpened(mainSidebar, true);

    case MainSidebarActions.CLOSE:
      return setMainSidebarOpened(mainSidebar, false);

    case MainSidebarActions.TOGGLE:
      return setMainSidebarOpened(mainSidebar, !mainSidebar.opened);

    default:
      return mainSidebar;
  }
};



